import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import Slider from "react-rangeslider";
import { Button } from "react-bootstrap";

import NavBar from "../../../components/NavBar/NavBar";
import StartQuoteBtn from "../../../components/StartQuoteBtn/StartQuoteBtn";

import "./FhaAffordabilityCalculator.css";
import "react-rangeslider/lib/index.css";

function FhaAffordabilityCalculator() {
  const { t } = useTranslation();
  const [desiredMortgage, setDesiredMortgage] = useState(100000);
  const [numTerm, setNumTerm] = useState(30);
  const [rate, setRate] = useState(4.5);
  const [usedLoan, setUsedLoan] = useState("no");
  const [propertyTax, setPropertyTax] = useState(1.2);
  const [propertyTaxValue, setPropertyTaxValue] = useState(0);
  const [nonMortgageDebt, setNonMortgageDebt] = useState(0.35);
  const [nonMortgageDebtValue, setNonMortgageDebtValue] = useState(0);
  const [monthlyPayment, setMonthlyPayment] = useState(0);
  const [totalPayment, setTotalPayment] = useState(0);
  const [monthlyIncome, setMonthlyIncome] = useState(0);
  const [annualIncome, setAnnualIncome] = useState(0);

  const roundToTwoDecimal = (value) => {
    return parseFloat(value).toFixed(2);
  };

  const handleChangeDesiredMortgage = (value) => {
    if (value !== null) {
      setDesiredMortgage(roundToTwoDecimal(value));
    }
  };

  const handleChangeNumTerm = (e) => {
    if (e !== null) {
      setNumTerm(parseInt(e.currentTarget.getAttribute("value")));
    }
  };

  const handleChangeUsedLoan = (e) => {
    if (e !== null) {
      setUsedLoan(e.currentTarget.getAttribute("value"));
    }
  };

  const handleChangeRate = (value) => {
    if (value !== null) {
      setRate(roundToTwoDecimal(value));
    }
  };

  const handleChangePropertyTax = (value) => {
    if (value !== null) {
      setPropertyTax(roundToTwoDecimal(value));
    }
  };

  const handleChangeNonMortgageDebt = (value) => {
    if (value !== null) {
      setNonMortgageDebt(roundToTwoDecimal(value));
    }
  };

  useEffect(() => {
    let vaff = 0;

    if (usedLoan === "yes") {
      vaff = 0.033 * desiredMortgage;
    } else {
      vaff = 0.0215 * desiredMortgage;
    }

    let r = rate / 1200;
    let months = numTerm * 12;

    let tmp11 = 1 + r;
    for (let i = 1; i < months; i++) {
      tmp11 = tmp11 * (1 + r);
    }

    let tmpMonthlyPayment = (desiredMortgage * tmp11 * r) / (tmp11 - 1);

    let monthlyDebt = parseFloat(nonMortgageDebt) / 100;
    if (isNaN(monthlyDebt)) monthlyDebt = 0;
    monthlyDebt = monthlyDebt * desiredMortgage;

    let monthlyTax = propertyTax / 12;

    let tempTotalPayment =
      tmpMonthlyPayment + monthlyTax + monthlyDebt + vaff / months;

    let tempMonthlyIncome = tempTotalPayment / 0.5;
    let tempAnnualIncome = tempMonthlyIncome * 12;

    let temPropertyTaxValue = (desiredMortgage * propertyTax) / 100;
    let temNonMortgageDebtValue = (desiredMortgage * nonMortgageDebt) / 100;

    setPropertyTaxValue(roundToTwoDecimal(temPropertyTaxValue));
    setNonMortgageDebtValue(roundToTwoDecimal(temNonMortgageDebtValue));
    setMonthlyPayment(roundToTwoDecimal(tmpMonthlyPayment));
    setTotalPayment(roundToTwoDecimal(tempTotalPayment));
    setMonthlyIncome(roundToTwoDecimal(tempMonthlyIncome));
    setAnnualIncome(roundToTwoDecimal(tempAnnualIncome));
  }, [
    desiredMortgage,
    numTerm,
    rate,
    usedLoan,
    propertyTax,
    nonMortgageDebt,
    monthlyPayment,
    totalPayment,
    monthlyIncome,
    annualIncome,
    nonMortgageDebtValue,
    propertyTaxValue,
  ]);

  return (
    <>
      <NavBar />
      <div className="container">
        <div className="row d-flex justify-content-center mt-5">
          <div className="col-sm-7 center tool-btn-div">
            <ul>
              <li>
                <a href="/tools/fha-calculator">
                  <strong>{t("tools.home.btn_1")}</strong>
                </a>
              </li>
              <li className="tool-btn-active">
                <a href="/tools/fha-affordability-calculator">
                  <strong>{t("tools.home.btn_2")}</strong>
                </a>
              </li>
            </ul>
          </div>
        </div>
        <div className="row d-flex justify-content-center mb-5">
          <div className="col-sm-7 center">
            <img
              className="h-img"
              src="/images/tools/t-1.png"
              alt="tools"
            ></img>
            <h3 className="header-text-2">
              {t("tools.fhaAffordabilityCalculator.head")}
            </h3>
            <p>{t("tools.fhaAffordabilityCalculator.txt_1")}</p>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-7">
            <div className="row d-flex justify-content-center">
              <div className="col-sm-10 mt-3">
                <div className="row input-box-div">
                  <div className="col">
                    <p>{t("tools.fhaAffordabilityCalculator.txt_2")}</p>
                  </div>
                  <div className="col input-col">
                    <span>$</span>
                    <input
                      className="input-box"
                      disabled={true}
                      value={desiredMortgage}
                    />
                  </div>
                </div>

                <Slider
                  className="slider-input"
                  min={0}
                  max={500000}
                  step={1000}
                  value={desiredMortgage}
                  onChange={handleChangeDesiredMortgage}
                  tooltip={false}
                />

                <div className="row">
                  <div className="col">
                    <p>$0</p>
                  </div>
                </div>
                <hr className="hr-input" />
              </div>
            </div>
            <div className="row d-flex justify-content-center">
              <div className="col-sm-10 mt-3">
                <div className="row input-box-div">
                  <div className="col">
                    <p>{t("tools.fhaAffordabilityCalculator.txt_3")}</p>
                  </div>
                </div>
                <div className="row input-box-div">
                  <div className="col mb-3 mb-3">
                    <div
                      className={
                        numTerm === 15
                          ? "checkbox-button checkbox-button-active"
                          : "checkbox-button"
                      }
                      value={15}
                      onClick={handleChangeNumTerm}
                    >
                      {t("tools.fhaAffordabilityCalculator.txt_15")}
                    </div>
                  </div>
                  <div className="col mb-3">
                    <div
                      className={
                        numTerm === 30
                          ? "checkbox-button checkbox-button-active"
                          : "checkbox-button"
                      }
                      value={30}
                      onClick={handleChangeNumTerm}
                    >
                      {t("tools.fhaAffordabilityCalculator.txt_16")}
                    </div>
                  </div>
                </div>
                <hr className="hr-input" />
              </div>
            </div>
            <div className="row d-flex justify-content-center">
              <div className="col-sm-10 mt-3">
                <div className="row input-box-div">
                  <div className="col">
                    <p>{t("tools.fhaAffordabilityCalculator.txt_4")}</p>
                  </div>
                  <div className="col input-col">
                    <input className="input-box" disabled={true} value={rate} />
                    <span>%</span>
                  </div>
                </div>

                <Slider
                  className="slider-input"
                  min={3}
                  max={10}
                  step={0.005}
                  value={rate}
                  onChange={handleChangeRate}
                  tooltip={false}
                />

                <div className="row">
                  <div className="col">
                    <p>3%</p>
                  </div>
                  <div className="col input-box-bottom-right">
                    <p>10%</p>
                  </div>
                </div>
                <hr className="hr-input" />
              </div>
            </div>
            <div className="row d-flex justify-content-center">
              <div className="col-sm-10 mt-3">
                <div className="row input-box-div">
                  <div className="col">
                    <p>{t("tools.fhaAffordabilityCalculator.txt_5")}</p>
                  </div>
                </div>
                <div className="row input-box-div">
                  <div className="col mb-3 mb-3">
                    <div
                      className={
                        usedLoan === "no"
                          ? "checkbox-button checkbox-button-active"
                          : "checkbox-button"
                      }
                      value={"no"}
                      onClick={handleChangeUsedLoan}
                    >
                      {t("tools.fhaAffordabilityCalculator.txt_17")}
                    </div>
                  </div>
                  <div className="col mb-3">
                    <div
                      className={
                        usedLoan === "yes"
                          ? "checkbox-button checkbox-button-active"
                          : "checkbox-button"
                      }
                      value={"yes"}
                      onClick={handleChangeUsedLoan}
                    >
                      {t("tools.fhaAffordabilityCalculator.txt_18")}
                    </div>
                  </div>
                </div>
                <hr className="hr-input" />
              </div>
            </div>
            <div className="row d-flex justify-content-center">
              <div className="col-sm-10 mt-3">
                <div className="row input-box-div">
                  <div className="col">
                    <p>{t("tools.fhaAffordabilityCalculator.txt_6")}</p>
                  </div>
                  <div className="col input-col">
                    <span>{`$${propertyTaxValue}/yr`}</span>
                    <input
                      className="input-box"
                      disabled={true}
                      value={propertyTax}
                    />
                    <span>%</span>
                  </div>
                </div>

                <Slider
                  className="slider-input"
                  min={0}
                  max={3}
                  step={0.01}
                  value={propertyTax}
                  onChange={handleChangePropertyTax}
                  tooltip={false}
                />

                <div className="row">
                  <div className="col">
                    <p>0%</p>
                  </div>
                  <div className="col input-box-bottom-right">
                    <p>3%</p>
                  </div>
                </div>
                <hr className="hr-input" />
              </div>
            </div>
            <div className="row d-flex justify-content-center">
              <div className="col-sm-10 mt-3">
                <div className="row input-box-div">
                  <div className="col">
                    <p>{t("tools.fhaAffordabilityCalculator.txt_7")}</p>
                  </div>
                  <div className="col input-col">
                    <span>{`$${nonMortgageDebtValue}/mo`}</span>
                    <input
                      className="input-box"
                      disabled={true}
                      value={nonMortgageDebt}
                    />
                    <span>%</span>
                  </div>
                </div>

                <Slider
                  className="slider-input"
                  min={0}
                  max={0.5}
                  step={0.01}
                  value={nonMortgageDebt}
                  onChange={handleChangeNonMortgageDebt}
                  tooltip={false}
                />

                <div className="row">
                  <div className="col">
                    <p>0%</p>
                  </div>
                  <div className="col input-box-bottom-right">
                    <p>0.5%</p>
                  </div>
                </div>
                <hr className="hr-input" />
              </div>
            </div>
          </div>
          <div className="col-sm-4">
            <div className="row">
              <div className="col center">
                <hr className="hr-input" />
                <p>{t("tools.fhaAffordabilityCalculator.txt_8")}</p>
                <h1>
                  <b>
                    $
                    {`${monthlyPayment}/ ${t(
                      "tools.fhaAffordabilityCalculator.txt_9"
                    )}`}
                  </b>
                </h1>
                <Button href="/start" variant="warning">
                  {t("quoteReq.btn")}
                </Button>
                <hr className="hr-input" />
              </div>
            </div>
            <div className="row">
              <div className="col center result-div">
                <ul>
                  <li>
                    <strong>
                      {t("tools.fhaAffordabilityCalculator.txt_10")}
                    </strong>
                    <span class="result_purchase_price">
                      $
                      {`${totalPayment}/ ${t(
                        "tools.fhaAffordabilityCalculator.txt_9"
                      )}`}
                    </span>
                  </li>
                  <li>
                    <strong>
                      {t("tools.fhaAffordabilityCalculator.txt_11")}
                    </strong>
                    <span class="result_fundingfee">
                      $
                      {`${monthlyIncome}/ ${t(
                        "tools.fhaAffordabilityCalculator.txt_9"
                      )}`}
                    </span>
                  </li>
                  <li>
                    <strong>
                      {t("tools.fhaAffordabilityCalculator.txt_12")}
                    </strong>
                    <span class="result_amount_financed">
                      $
                      {`${annualIncome}/  ${t(
                        "tools.fhaAffordabilityCalculator.txt_9"
                      )}`}
                    </span>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-10">
            <p>{t("tools.fhaAffordabilityCalculator.txt_13")}</p>
            <p>{t("tools.fhaAffordabilityCalculator.txt_14")}</p>
          </div>
        </div>
      </div>
      <StartQuoteBtn />
    </>
  );
}

export default FhaAffordabilityCalculator;
